import Toast from 'vux/src/components/toast/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'order_detail',
directives: {
TransferDom
},
components: {
Toast,
Scroller,
Rater,
XTextarea,
XDialog,
Previewer
},
data() {
return {
no: '',
recpData: {},
workPrice: 0,
materialPrice: 0,
previewerImg: [],
recpte_img: [],
rater: 0,
comment: '',
show: false,
currentPre: ''
}
},
activated: function() {
var self = this;
self.no = self.$route.params.id;
self.fetchData(self.no);
},
deactivated: function() {
var self = this;
self.show = false;
},
methods: {
fetchData: function(id) {

var self = this;
self.xarpost('ShopOrderRecp/getorderRecpInfoFront', {
'order_sn': id,
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
return;
}

var newData = (d.data == undefined) ? [] : d.data;
self.setData(newData);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
setData(data) {
var self = this;
self.recpData = data;
self.recpte_img = [];
for(var index in data.recpte_img) {
self.recpte_img.push({
src: data.recpte_img[index]
});
}
var jobs = data.jobs;

var workPrice = 0,
materialPrice = 0;
for(var job in jobs) {
if(jobs[job].job_type == 0) {
workPrice += parseFloat(jobs[job].server_totalprice);
}
if(jobs[job].job_type == 1) {
materialPrice += parseFloat(jobs[job].server_totalprice);
}
}
self.workPrice = workPrice;
self.materialPrice = materialPrice;
if(data.order_status >= 10 && data.order_status < 20) {
self.show = true;
}
},
showJobs: function() {
this.show = true;
},
closeDialog: function() {
this.show = false;
},
confirmTips: function() {
var self = this;
self.show = false;
self.xarpost('ShopOrderRecp/customerConfirm', {
'order_sn': self.no,
}).then(function(d) {
let {
data,
error,
message
} = d;
self.$vux.toast.show({
text: d.message,
time: 3000
});
if(d.error != 0) {
return;
}

var newData = (d.data == undefined) ? [] : d.data;
self.setConfirm(newData);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
setConfirm: function(data) {
self.$vux.toast.show({
text: '确认成功！',
type: 'success',
time: 3000
});
this.recpData.order_status = 20;
},
showRecpImg: function(index) {
var self = this;
this.$refs.recpPreviewer.show(index);
},
pay: function() {
var self = this;
self.show = false;
self.xarpost('ShopOrderRecp/pay', {
'order_sn': self.no,
}).then(function(d) {
let {
data,
error,
message
} = d;
self.$vux.toast.show({
text: d.message,
time: 3000
});
if(d.error != 0) {
return;
}

var pay = data.data;
self.$wechat.chooseWXPay({
timestamp: pay.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: pay.nonceStr, // 支付签名随机串，不长于 32 位
package: pay.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: pay.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: pay.paySign, // 支付签名
success: function(res) {
resfun(res)
},
cancel: function(res) {
showtoast('取消支付')
rejfun(res)
},
fail: function(res) {
showtoast('支付失败,请联系管理员')
rejfun(res)
}
});
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
}
}
}


